import React from "react"
import { getQueryVariable } from '../util'

export default function LangToggle() {

	const [langTH, setLangTH] = React.useState(false)

	function toggleLang() {
		setLangTH(!langTH)
	}

	React.useEffect(() => {
		const querylang = getQueryVariable('lang')
		if (querylang === 'th' || querylang === 'en') {
			setLangTH(querylang === 'th')
		}
		else {
			setLangTH(localStorage.getItem('artlang') === "lang--th")
		}
	}, [])

	React.useEffect(() => {
		if (typeof document !== 'undefined') {
			document.querySelector(".content").classList.remove("lang--" + (!langTH ? "th" : "en"));
			document.querySelector(".content").classList.add("lang--" + (langTH ? "th" : "en"));
			localStorage.setItem('artlang', "lang--" + (langTH ? "th" : "en"))
			window.history.pushState('', '', '?lang=' + (langTH ? 'th' : 'en'))
		}
	}, [langTH])

	return (
		<React.Fragment>
			<span className="post-langs">
				<button className="tag-item lang-item" onClick={(toggleLang)}>
					<span className="lang-item-th">Th</span> / <span className="lang-item-en">En</span>
				</button>
			</span>
		</React.Fragment>
	)

}