import React from "react"
import "./Footer.scss"
import { useStaticQuery, graphql, Link } from "gatsby"
import { FaGithub, FaFacebook } from 'react-icons/fa';
import { RandomHLine } from "react-random-shapes"

export default function Footer() {

	const data = useStaticQuery(graphql`
		query {
			site {
				siteMetadata {
					siteTitle
					author
				}
			}
		}
	`)

	const {
		siteTitle,
		author,
	} = data.site.siteMetadata

	const waveWidth = 10000
	const waveHeight = 80

	return (
		<div className="footer-wrapper">
			<div className="footer-wave-layout">
				<div />
				<div className="footer-mid">
					<div className="footer-wave-container">
						<RandomHLine width={waveWidth} height={waveHeight} className="footer-svg"
																			 options={{numControls: 10,
																			 					 styleTop: "none",
																			 					 styleMid: "none",
																			 					 classNameBottom: "footer-wave",
																			 					 angleWindowSize: Math.PI / 16
																			 				 }} />
					</div>
				</div>
				<div className="invisible" />
			</div>
			<div className="footer-container">
				<div id="footer">
					<div className="footer-left">
						<p><span className="bold"><Link to="/">{siteTitle}</Link></span></p>
						<p>
							Powered by <a href="https://www.gatsbyjs.org/" target="_blank" rel="noopener noreferrer">Gatsby</a><br />
							Copyright © 2020 by {author}. All rights reserved.
						</p>
					</div>
					<div className="footer-right">
						<span className="footer-icon">
							<a href="https://github.com/artt" target="_blank" rel="noopener noreferrer" aria-label="github"><FaGithub /></a>
						</span>
						<span className="footer-icon">
							<a href="https://www.facebook.com/art.tosborvorn" target="_blank" rel="noopener noreferrer" aria-label="facebook"><FaFacebook /></a>
						</span>
					</div>
				</div>
			</div>
		</div>
	)

}