import React from "react"
import { MDXProvider } from "@mdx-js/react"
import { preToCodeBlock } from "mdx-utils"
import Code from "./Code"
import Header from "./Header"
import Footer from "./Footer"
import "../styles/main.scss"
import { LazyPlotly, LazyHighcharts } from './Plotly'

const components = {
	pre: preProps => {
		const props = preToCodeBlock(preProps)
		let mode = ""
		if (typeof document !== 'undefined')
			mode = document.body.className
		if (props) {
			return <Code {...props} mode={mode} />
		} else {
			return <pre {...preProps} mode={mode} />
		}
	},
	h1: props => <React.Fragment><div><div className="anchor-t" id={props.id} /><h1 {...props}>{props.children}</h1></div></React.Fragment>,
	h2: props => <React.Fragment><div><div className="anchor-t" id={props.id} /><h2 {...props}>{props.children}</h2></div></React.Fragment>,
	h3: props => <React.Fragment><div><div className="anchor-t" id={props.id} /><h3 {...props}>{props.children}</h3></div></React.Fragment>,
	h4: props => <React.Fragment><div><div className="anchor-t" id={props.id} /><h4 {...props}>{props.children}</h4></div></React.Fragment>,
	h5: props => <React.Fragment><div><div className="anchor-t" id={props.id} /><h5 {...props}>{props.children}</h5></div></React.Fragment>,
	h6: props => <React.Fragment><div><div className="anchor-t" id={props.id} /><h6 {...props}>{props.children}</h6></div></React.Fragment>,
	LazyPlotly,
	LazyHighcharts
}

if (typeof window !== 'undefined') {
  // Make scroll behavior of internal links smooth
  // eslint-disable-next-line global-require
  require('smooth-scroll')('a[href*="#"]', {
  	speed: 500,
  	speedAsDuration: false,
  	durationMax: 1000,
  	// offset: 10
  });
}

const Layout = ({ children }) => {

	return(
		<div id="main">
			<div className="non-footer">
				<Header />
				<MDXProvider components={components}>
					<div className="layout-content">
						{children}
					</div>
				</MDXProvider>
			</div>
			<Footer />
		</div>
	)
}


export default Layout
