import React from "react"
import DayNight from "../DayNight"
import { useStaticQuery, graphql, Link } from "gatsby"
import { FaBars } from 'react-icons/fa'
import "./Header.scss";

function Header() {


	const data = useStaticQuery(
		graphql`
			query {
				site {
					siteMetadata {
						siteTitle
						navItems {
							name
							url
						}
					}
				}
			}
		`
	)

	const menu = React.createRef()
	const header = React.createRef()

	let oldY = 0
	let lastTurn = 0
	let oldScroll = 0
	let headerPos = 0

	function handleClick() {
		if (window.innerWidth <= 700 && window.pageYOffset !== headerPos) {
			// document.documentElement.style.setProperty('--header', window.pageYOffset + "px")
			document.documentElement.style.setProperty('--header-pos', 'absolute')
			window.scrollTo({ top: headerPos, behavior: 'smooth' })
			document.documentElement.style.setProperty('--header', "0px")
			document.documentElement.style.setProperty('--header-pos', 'fixed')
		}
		else {
			// window.scrollTo({ top: 0, behavior: 'smooth' })
		}
	}

	function handleScroll() {
		const headerHeight = header.current.offsetHeight
		const y = window.pageYOffset

		// console.log("oldY", oldY)
		// console.log("y", y)
		// console.log("headerPos", headerPos)
		// console.log("lastTurn", lastTurn)
		// console.log("oldScroll", oldScroll)
		if (oldY > y) {
			if (oldScroll === 1) {
				// switch to scroll up
				oldScroll = 0
				if (y - lastTurn > headerHeight) {
					// console.log("update lastTurn to", lastTurn)
					document.documentElement.style.setProperty('--header', (y - headerHeight) + "px")
					document.documentElement.style.setProperty('--header-pos', 'absolute')
					headerPos = y - headerHeight
				}
				lastTurn = y
			}
			else {
				// continue to scroll up
				// console.log("up... diff =", lastTurn - y)
				const margin = oldY - y
				if (lastTurn - y > headerHeight - margin || y < headerPos) {
					// can hide
					document.documentElement.style.setProperty('--header', "0px")
					document.documentElement.style.setProperty('--header-pos', 'fixed')
					headerPos = y
				}
			}
		}
		else if (oldY < y) {
			if (header.current.classList.contains("menu-hide")) {
				if (oldScroll === 0) {
					// switch to scroll down
					// console.log("switch to down")
					oldScroll = 1
					// console.log("update lastTurn to", lastTurn)
					if (lastTurn - y > headerHeight) {
						document.documentElement.style.setProperty('--header', y + "px")
						document.documentElement.style.setProperty('--header-pos', 'absolute')
						headerPos = y
					}
					else if (lastTurn < y) {
						document.documentElement.style.setProperty('--header', Math.max(0, y - headerHeight) + "px")
						document.documentElement.style.setProperty('--header-pos', 'absolute')
						headerPos = Math.max(0, y - headerHeight)
					}
					lastTurn = y
				}
				else {
					document.documentElement.style.setProperty('--header', headerPos + "px")
						document.documentElement.style.setProperty('--header-pos', 'absolute')
						// headerPos = y
					// document.documentElement.style.setProperty('--header', (window.pageYOffset - 40) + "px")
					// continue to scroll down
				}
			}
		}
		oldY = y
	}

	if (typeof window !== 'undefined') {
		oldY = window.pageYOffset
		document.documentElement.style.setProperty('--header', "0px")
		document.documentElement.style.setProperty('--header-pos', 'absolute')
		window.onscroll = handleScroll
		// scrollStop(handleStopScroll)
	}

	function toggleMenu() {
		console.log("toggle")
		headerPos = window.pageYOffset
		header.current.classList.toggle("menu-hide")
	}

	return(
		<React.Fragment>
			<div className="header-container menu-hide" ref={header} onClick={handleClick}>
				<div className="header">
					<div className="header-toprow">
						<div className="header-title">
							<Link to={`/`}>
								{data.site.siteMetadata.siteTitle}
							</Link>
						</div>
						<div className="main-nav">
							<div className="nav">
							{
								data.site.siteMetadata.navItems.map((item, i) => (
									<Link to={item.url} key={i} className="nav-item nav-test" activeClassName="nav-active" partiallyActive={true} label={item.name}>{item.name}</Link>
								))
							}
							</div>
						</div>
						<div className="dark-mode-toggle">
							<DayNight />
						</div>						
						<div className="min-nav" onClick={toggleMenu}>
							<FaBars /> 
						</div>
					</div>
				</div>
				<div id="menu" ref={menu}>
					<div className="nav menu-nav">
					{
						data.site.siteMetadata.navItems.map((item, i) => (
							<div key={"nav" + i}>
								<Link to={item.url} key={i} className="menu-nav-item nav-test" activeClassName="nav-active menu-nav-active" partiallyActive={true} label={item.name}>{item.name}</Link>
							</div>
						))
					}
					</div>
				</div>
			</div>
		</React.Fragment>
	)

}

export default Header;