import React from "react"
import { Link } from "gatsby"
import LangToggle from "./LangToggle"

export default function PostDetails({ frontmatter, timeToRead, showLang, showTags }) {

	return(
		<div className="post-details">
			<span className="post-date">{frontmatter.date}</span>
			{timeToRead &&
				<React.Fragment>
					&nbsp;·&nbsp;
					<span className="post-time-to-read">{timeToRead}-min read</span>
				</React.Fragment>
			}
			{showLang && frontmatter.lang && frontmatter.lang.length > 1 &&
				<React.Fragment>
					&nbsp;·&nbsp;
					<LangToggle />
				</React.Fragment>
			}
			{showTags && frontmatter.tags &&
				<React.Fragment>
					&nbsp;·&nbsp;
					<span className="post-tags">
						Tags: {
							<React.Fragment>
								{frontmatter.tags.map((tag, i) => {
									return(
										<React.Fragment key={i}>
											{i > 0 && ", "}
											{/* need to add link here https://www.gatsbyjs.org/docs/adding-tags-and-categories-to-blog-posts/ */}
											<Link to={`/blog?tags=${tag}`} className="tag-item">{tag}</Link>
										</React.Fragment>
									)
								})}
							</React.Fragment>
						}
					</span>
				</React.Fragment>
			}
		</div>
	)
}